import { useEffect, useRef, useState, useCallback } from 'react';
import { imageDataToBlob } from '../lib/imageUtils';

interface ResultViewerProps {
    imageData: ImageData;
}

export function ResultViewer({ imageData }: ResultViewerProps) {
    const canvasRef = useRef<HTMLCanvasElement>(null);
    const [isDownloading, setIsDownloading] = useState(false);

    useEffect(() => {
        const canvas = canvasRef.current;
        if (!canvas) return;

        canvas.width = imageData.width;
        canvas.height = imageData.height;
        const ctx = canvas.getContext('2d')!;
        ctx.putImageData(imageData, 0, 0);
    }, [imageData]);

    const handleDownload = useCallback(async (format: 'png' | 'jpeg') => {
        setIsDownloading(true);
        try {
            const mimeType = format === 'png' ? 'image/png' : 'image/jpeg';
            const blob = await imageDataToBlob(imageData, mimeType, 0.95);

            const url = URL.createObjectURL(blob);
            const link = document.createElement('a');
            link.href = url;
            link.download = `hdr_merged_${Date.now()}.${format}`;
            document.body.appendChild(link);
            link.click();
            document.body.removeChild(link);
            URL.revokeObjectURL(url);
        } catch (error) {
            console.error('Download failed:', error);
        } finally {
            setIsDownloading(false);
        }
    }, [imageData]);

    const handleCopyToClipboard = useCallback(async () => {
        try {
            const blob = await imageDataToBlob(imageData, 'image/png');
            await navigator.clipboard.write([
                new ClipboardItem({ 'image/png': blob })
            ]);
        } catch (error) {
            console.error('Copy failed:', error);
        }
    }, [imageData]);

    return (
        <div className="result-viewer">
            <div className="result-preview">
                <canvas
                    ref={canvasRef}
                    className="result-canvas"
                />
            </div>

            <div className="result-info">
                <span className="result-dimensions">
                    {imageData.width}  {imageData.height} pixels
                </span>
            </div>

            <div className="result-actions">
                <button
                    className="btn btn-primary"
                    onClick={() => handleDownload('png')}
                    disabled={isDownloading}
                >
                    <svg viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                        <path d="M21 15v4a2 2 0 01-2 2H5a2 2 0 01-2-2v-4" />
                        <polyline points="7 10 12 15 17 10" />
                        <line x1="12" y1="15" x2="12" y2="3" />
                    </svg>
                    Download PNG
                </button>

                <button
                    className="btn btn-secondary"
                    onClick={() => handleDownload('jpeg')}
                    disabled={isDownloading}
                >
                    <svg viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                        <path d="M21 15v4a2 2 0 01-2 2H5a2 2 0 01-2-2v-4" />
                        <polyline points="7 10 12 15 17 10" />
                        <line x1="12" y1="15" x2="12" y2="3" />
                    </svg>
                    Download JPEG
                </button>

                <button
                    className="btn btn-ghost"
                    onClick={handleCopyToClipboard}
                >
                    <svg viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                        <rect x="9" y="9" width="13" height="13" rx="2" ry="2" />
                        <path d="M5 15H4a2 2 0 01-2-2V4a2 2 0 012-2h9a2 2 0 012 2v1" />
                    </svg>
                    Copy
                </button>
            </div>
        </div>
    );
}
