import { useState, useCallback } from 'react'
import { ImageUploader } from './components/ImageUploader'
import { ImagePreview } from './components/ImagePreview'
import { MergeSettings } from './components/MergeSettings'
import { MergeProgress } from './components/MergeProgress'
import { ResultViewer } from './components/ResultViewer'
import type { MergeOptions } from './lib/exposureFusion'
import type { LoadedImage } from './types'
import FusionWorker from './workers/fusion.worker?worker'
import type { FusionRequest, FusionResponse } from './workers/fusion.worker'

function App() {
    const [images, setImages] = useState<LoadedImage[]>([])
    const [mergeOptions, setMergeOptions] = useState<MergeOptions>({
        contrastWeight: 1.0,
        saturationWeight: 1.0,
        exposureWeight: 1.0,
        pyramidLevels: 0, // 0 = auto
        align: false
    })
    const [isProcessing, setIsProcessing] = useState(false)
    const [progress, setProgress] = useState({ stage: '', percent: 0 })
    const [result, setResult] = useState<ImageData | null>(null)

    const handleImagesLoaded = useCallback((newImages: LoadedImage[]) => {
        setImages(prev => [...prev, ...newImages])
        setResult(null) // Clear previous result when new images are added
    }, [])

    const handleRemoveImage = useCallback((index: number) => {
        setImages(prev => prev.filter((_, i) => i !== index))
        setResult(null)
    }, [])

    const handleReorderImages = useCallback((fromIndex: number, toIndex: number) => {
        setImages(prev => {
            const newImages = [...prev]
            const [removed] = newImages.splice(fromIndex, 1)
            newImages.splice(toIndex, 0, removed)
            return newImages
        })
    }, [])

    const handleMerge = useCallback(async () => {
        if (images.length < 2) return

        setIsProcessing(true)
        setProgress({ stage: 'Starting merge (in worker)...', percent: 0 })

        try {
            // Clone ImageData for worker (images state must remain valid for UI)
            const imageDataArray = images.map(img => img.imageData)

            const merged = await new Promise<ImageData>((resolve, reject) => {
                const worker = new FusionWorker();

                worker.onmessage = (e: MessageEvent<FusionResponse>) => {
                    const msg = e.data;
                    if (msg.type === 'PROGRESS') {
                        setProgress({ stage: msg.stage, percent: msg.percent });
                    } else if (msg.type === 'COMPLETE') {
                        resolve(msg.result);
                        worker.terminate();
                    } else if (msg.type === 'ERROR') {
                        reject(new Error(msg.error));
                        worker.terminate();
                    }
                };

                worker.onerror = (err: ErrorEvent) => {
                    reject(new Error('Worker error: ' + err.message));
                    worker.terminate();
                };

                worker.postMessage({
                    type: 'START_MERGE',
                    images: imageDataArray,
                    options: mergeOptions
                } as FusionRequest);
            });

            setResult(merged)
        } catch (error) {
            console.error('Merge failed:', error)
            setProgress({ stage: `Error: ${error instanceof Error ? error.message : 'Unknown error'}`, percent: 0 })
        } finally {
            setIsProcessing(false)
        }
    }, [images, mergeOptions])

    const handleClearAll = useCallback(() => {
        setImages([])
        setResult(null)
        setProgress({ stage: '', percent: 0 })
    }, [])

    return (
        <div className="app">
            <header className="app-header">
                <div className="logo">
                    <svg viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                        <rect x="3" y="3" width="18" height="18" rx="2" />
                        <circle cx="12" cy="12" r="3" />
                        <path d="M3 8h18M3 16h18M8 3v18M16 3v18" opacity="0.3" />
                    </svg>
                    <h1>Photo Stacker</h1>
                </div>
                <p className="subtitle">HDR Exposure Fusion</p>
            </header>

            <main className="app-main">
                <div className="workspace">
                    <section className="upload-section">
                        <ImageUploader onImagesLoaded={handleImagesLoaded} disabled={isProcessing} />
                    </section>

                    {images.length > 0 && (
                        <>
                            <section className="preview-section">
                                <div className="section-header">
                                    <h2>Source Images ({images.length})</h2>
                                    <button
                                        className="btn btn-ghost"
                                        onClick={handleClearAll}
                                        disabled={isProcessing}
                                    >
                                        Clear All
                                    </button>
                                </div>
                                <ImagePreview
                                    images={images}
                                    onRemove={handleRemoveImage}
                                    onReorder={handleReorderImages}
                                    disabled={isProcessing}
                                />
                            </section>

                            <section className="settings-section">
                                <h2>Merge Settings</h2>
                                <MergeSettings
                                    options={mergeOptions}
                                    onChange={setMergeOptions}
                                    disabled={isProcessing}
                                />
                                <button
                                    className="btn btn-primary btn-merge"
                                    onClick={handleMerge}
                                    disabled={isProcessing || images.length < 2}
                                >
                                    {isProcessing ? 'Processing...' : `Merge ${images.length} Images`}
                                </button>
                            </section>

                            {isProcessing && (
                                <section className="progress-section">
                                    <MergeProgress stage={progress.stage} percent={progress.percent} />
                                </section>
                            )}

                            {result && !isProcessing && (
                                <section className="result-section">
                                    <h2>Merged Result</h2>
                                    <ResultViewer imageData={result} />
                                </section>
                            )}
                        </>
                    )}
                </div>
            </main>

            <footer className="app-footer">
                <p>All processing happens locally in your browser. Your images never leave your device.</p>
            </footer>
        </div>
    )
}

export default App
