import { useCallback, useState } from 'react';
import * as UTIF from 'utif2';
import type { LoadedImage } from '../types';
import { imageToImageData, createThumbnail } from '../lib/imageUtils';

interface ImageUploaderProps {
    onImagesLoaded: (images: LoadedImage[]) => void;
    disabled?: boolean;
}

export function ImageUploader({ onImagesLoaded, disabled }: ImageUploaderProps) {
    const [isDragOver, setIsDragOver] = useState(false);
    const [isLoading, setIsLoading] = useState(false);
    const [loadingProgress, setLoadingProgress] = useState({ current: 0, total: 0 });

    const processFiles = useCallback(async (files: FileList) => {
        const imageFiles = Array.from(files).filter(file =>
            file.type.startsWith('image/') ||
            file.name.toLowerCase().match(/\.(arw|cr2|cr3|nef|dng|raf|orf|rw2|pef|srw|tiff?|jpe?g|png)$/)
        );

        if (imageFiles.length === 0) return;

        setIsLoading(true);
        setLoadingProgress({ current: 0, total: imageFiles.length });

        const loadedImages: LoadedImage[] = [];

        for (let i = 0; i < imageFiles.length; i++) {
            const file = imageFiles[i];
            setLoadingProgress({ current: i + 1, total: imageFiles.length });

            try {
                const imageData = await loadImageFile(file);
                const thumbnail = createThumbnail(imageData, 200);

                loadedImages.push({
                    id: crypto.randomUUID(),
                    name: file.name,
                    size: file.size,
                    type: file.type || 'image/tiff',
                    imageData,
                    thumbnail
                });
            } catch (error) {
                console.error(`Failed to load ${file.name}:`, error);
            }
        }

        setIsLoading(false);
        if (loadedImages.length > 0) {
            onImagesLoaded(loadedImages);
        }
    }, [onImagesLoaded]);

    const handleDragOver = useCallback((e: React.DragEvent) => {
        e.preventDefault();
        e.stopPropagation();
        if (!disabled) setIsDragOver(true);
    }, [disabled]);

    const handleDragLeave = useCallback((e: React.DragEvent) => {
        e.preventDefault();
        e.stopPropagation();
        setIsDragOver(false);
    }, []);

    const handleDrop = useCallback((e: React.DragEvent) => {
        e.preventDefault();
        e.stopPropagation();
        setIsDragOver(false);

        if (disabled) return;

        const files = e.dataTransfer.files;
        if (files.length > 0) {
            processFiles(files);
        }
    }, [disabled, processFiles]);

    const handleFileInput = useCallback((e: React.ChangeEvent<HTMLInputElement>) => {
        const files = e.target.files;
        if (files && files.length > 0) {
            processFiles(files);
        }
        // Reset input so same file can be selected again
        e.target.value = '';
    }, [processFiles]);

    return (
        <div
            className={`uploader ${isDragOver ? 'drag-over' : ''} ${disabled ? 'disabled' : ''} ${isLoading ? 'loading' : ''}`}
            onDragOver={handleDragOver}
            onDragLeave={handleDragLeave}
            onDrop={handleDrop}
        >
            {isLoading ? (
                <div className="uploader-loading">
                    <div className="spinner"></div>
                    <p>Loading images... {loadingProgress.current}/{loadingProgress.total}</p>
                </div>
            ) : (
                <>
                    <div className="uploader-icon">
                        <svg viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="1.5">
                            <path d="M4 16l4.586-4.586a2 2 0 012.828 0L16 16m-2-2l1.586-1.586a2 2 0 012.828 0L20 14" />
                            <rect x="3" y="3" width="18" height="18" rx="2" />
                            <circle cx="8.5" cy="8.5" r="1.5" fill="currentColor" />
                        </svg>
                    </div>
                    <div className="uploader-text">
                        <p className="uploader-title">Drop images here</p>
                        <p className="uploader-subtitle">or click to browse</p>
                    </div>
                    <div className="uploader-formats">
                        Supports JPEG, TIFF, PNG
                    </div>
                    <input
                        type="file"
                        accept="image/*,.tiff,.tif"
                        multiple
                        onChange={handleFileInput}
                        disabled={disabled}
                    />
                </>
            )}
        </div>
    );
}

/**
 * Check if file is a TIFF based on extension or MIME type
 */
function isTiffFile(file: File): boolean {
    const ext = file.name.toLowerCase();
    return ext.endsWith('.tiff') || ext.endsWith('.tif') ||
        file.type === 'image/tiff' || file.type === 'image/tif';
}

/**
 * Load a TIFF file using utif2 library
 */
async function loadTiffFile(file: File): Promise<ImageData> {
    const arrayBuffer = await file.arrayBuffer();
    const ifds = UTIF.decode(arrayBuffer);

    if (ifds.length === 0) {
        throw new Error('No images found in TIFF file');
    }

    // Decode the first image
    UTIF.decodeImage(arrayBuffer, ifds[0]);
    const firstImage = ifds[0];

    // Get RGBA data
    const rgba = UTIF.toRGBA8(firstImage);

    // Create ImageData
    const imageData = new ImageData(
        new Uint8ClampedArray(rgba),
        firstImage.width,
        firstImage.height
    );

    return imageData;
}

/**
 * Loads an image file and returns ImageData
 * Handles TIFF files with utif2, other formats use canvas
 */
async function loadImageFile(file: File): Promise<ImageData> {
    // Check if it's a RAW file
    const isRaw = file.name.toLowerCase().match(/\.(arw|cr2|cr3|nef|dng|raf|orf|rw2|pef|srw)$/);

    if (isRaw) {
        throw new Error(
            `RAW file detected (${file.name}). Please export your RAW files as TIFF for highest quality.`
        );
    }

    // Handle TIFF files with utif2
    if (isTiffFile(file)) {
        return loadTiffFile(file);
    }

    // For other formats (JPEG, PNG), use native browser decoding
    return new Promise((resolve, reject) => {
        const img = new Image();
        img.onload = () => {
            URL.revokeObjectURL(img.src);
            try {
                const imageData = imageToImageData(img);
                resolve(imageData);
            } catch (error) {
                reject(error);
            }
        };
        img.onerror = () => {
            URL.revokeObjectURL(img.src);
            reject(new Error(`Failed to load image: ${file.name}`));
        };
        img.src = URL.createObjectURL(file);
    });
}
