interface MergeProgressProps {
    stage: string;
    percent: number;
}

export function MergeProgress({ stage, percent }: MergeProgressProps) {
    return (
        <div className="merge-progress">
            <div className="progress-bar-container">
                <div
                    className="progress-bar-fill"
                    style={{ width: `${percent}%` }}
                />
            </div>
            <div className="progress-info">
                <span className="progress-stage">{stage}</span>
                <span className="progress-percent">{Math.round(percent)}%</span>
            </div>
        </div>
    );
}
