/**
 * Image utilities for canvas operations and image manipulation
 */

/**
 * Creates an ImageData object from an image element
 */
export function imageToImageData(img: HTMLImageElement): ImageData {
    const canvas = document.createElement('canvas');
    canvas.width = img.naturalWidth;
    canvas.height = img.naturalHeight;
    const ctx = canvas.getContext('2d')!;
    ctx.drawImage(img, 0, 0);
    return ctx.getImageData(0, 0, canvas.width, canvas.height);
}

/**
 * Creates a data URL thumbnail from ImageData
 */
export function createThumbnail(imageData: ImageData, maxSize: number = 200): string {
    const { width, height } = imageData;
    const scale = Math.min(maxSize / width, maxSize / height, 1);
    const thumbWidth = Math.round(width * scale);
    const thumbHeight = Math.round(height * scale);

    const canvas = document.createElement('canvas');
    canvas.width = thumbWidth;
    canvas.height = thumbHeight;
    const ctx = canvas.getContext('2d')!;

    // Create temporary canvas with original size
    const tempCanvas = document.createElement('canvas');
    tempCanvas.width = width;
    tempCanvas.height = height;
    const tempCtx = tempCanvas.getContext('2d')!;
    tempCtx.putImageData(imageData, 0, 0);

    // Draw scaled version
    ctx.drawImage(tempCanvas, 0, 0, thumbWidth, thumbHeight);
    return canvas.toDataURL('image/jpeg', 0.8);
}

/**
 * Converts ImageData to a downloadable Blob
 */
export function imageDataToBlob(imageData: ImageData, type: string = 'image/png', quality: number = 0.95): Promise<Blob> {
    return new Promise((resolve, reject) => {
        const canvas = document.createElement('canvas');
        canvas.width = imageData.width;
        canvas.height = imageData.height;
        const ctx = canvas.getContext('2d')!;
        ctx.putImageData(imageData, 0, 0);
        canvas.toBlob(
            (blob) => {
                if (blob) resolve(blob);
                else reject(new Error('Failed to create blob'));
            },
            type,
            quality
        );
    });
}

/**
 * Creates an ImageData object with specified dimensions
 */
export function createImageData(width: number, height: number): ImageData {
    return new ImageData(width, height);
}

/**
 * Clones an ImageData object
 */
export function cloneImageData(source: ImageData): ImageData {
    const data = new Uint8ClampedArray(source.data);
    return new ImageData(data, source.width, source.height);
}

/**
 * Gets pixel value at (x, y) from ImageData
 * Returns [r, g, b, a] in 0-255 range
 */
export function getPixel(imageData: ImageData, x: number, y: number): [number, number, number, number] {
    const idx = (y * imageData.width + x) * 4;
    return [
        imageData.data[idx],
        imageData.data[idx + 1],
        imageData.data[idx + 2],
        imageData.data[idx + 3]
    ];
}

/**
 * Sets pixel value at (x, y) in ImageData
 */
export function setPixel(imageData: ImageData, x: number, y: number, r: number, g: number, b: number, a: number = 255): void {
    const idx = (y * imageData.width + x) * 4;
    imageData.data[idx] = r;
    imageData.data[idx + 1] = g;
    imageData.data[idx + 2] = b;
    imageData.data[idx + 3] = a;
}

/**
 * Converts sRGB value (0-255) to linear light value (0-1)
 */
export function srgbToLinear(value: number): number {
    const v = value / 255;
    return v <= 0.04045 ? v / 12.92 : Math.pow((v + 0.055) / 1.055, 2.4);
}

/**
 * Converts linear light value (0-1) to sRGB value (0-255)
 */
export function linearToSrgb(value: number): number {
    const v = Math.max(0, Math.min(1, value));
    const srgb = v <= 0.0031308 ? v * 12.92 : 1.055 * Math.pow(v, 1 / 2.4) - 0.055;
    return Math.round(srgb * 255);
}

/**
 * Resizes ImageData to new dimensions using bilinear interpolation
 */
export function resizeImageData(imageData: ImageData, newWidth: number, newHeight: number): ImageData {
    const canvas = document.createElement('canvas');
    canvas.width = newWidth;
    canvas.height = newHeight;
    const ctx = canvas.getContext('2d')!;

    // Put original image on temp canvas
    const tempCanvas = document.createElement('canvas');
    tempCanvas.width = imageData.width;
    tempCanvas.height = imageData.height;
    const tempCtx = tempCanvas.getContext('2d')!;
    tempCtx.putImageData(imageData, 0, 0);

    // Draw scaled
    ctx.imageSmoothingEnabled = true;
    ctx.imageSmoothingQuality = 'high';
    ctx.drawImage(tempCanvas, 0, 0, newWidth, newHeight);

    return ctx.getImageData(0, 0, newWidth, newHeight);
}
