import { useCallback, useState, useRef } from 'react';
import type { LoadedImage } from '../types';

interface ImagePreviewProps {
    images: LoadedImage[];
    onRemove: (index: number) => void;
    onReorder: (fromIndex: number, toIndex: number) => void;
    disabled?: boolean;
}

export function ImagePreview({ images, onRemove, onReorder, disabled }: ImagePreviewProps) {
    const [draggedIndex, setDraggedIndex] = useState<number | null>(null);
    const [dragOverIndex, setDragOverIndex] = useState<number | null>(null);
    const dragNodeRef = useRef<HTMLDivElement | null>(null);

    const formatSize = (bytes: number) => {
        if (bytes > 1024 * 1024) {
            return `${(bytes / (1024 * 1024)).toFixed(1)} MB`;
        }
        return `${(bytes / 1024).toFixed(0)} KB`;
    };

    const handleRemove = useCallback((index: number) => {
        if (!disabled) {
            onRemove(index);
        }
    }, [disabled, onRemove]);

    const handleDragStart = useCallback((e: React.DragEvent<HTMLDivElement>, index: number) => {
        if (disabled) return;

        setDraggedIndex(index);
        dragNodeRef.current = e.currentTarget;

        // Set drag image
        e.dataTransfer.effectAllowed = 'move';
        e.dataTransfer.setData('text/plain', index.toString());

        // Add dragging class after a small delay
        setTimeout(() => {
            if (dragNodeRef.current) {
                dragNodeRef.current.classList.add('dragging');
            }
        }, 0);
    }, [disabled]);

    const handleDragEnd = useCallback(() => {
        setDraggedIndex(null);
        setDragOverIndex(null);

        if (dragNodeRef.current) {
            dragNodeRef.current.classList.remove('dragging');
        }
        dragNodeRef.current = null;
    }, []);

    const handleDragOver = useCallback((e: React.DragEvent<HTMLDivElement>, index: number) => {
        e.preventDefault();
        if (disabled || draggedIndex === null || draggedIndex === index) return;

        setDragOverIndex(index);
        e.dataTransfer.dropEffect = 'move';
    }, [disabled, draggedIndex]);

    const handleDragLeave = useCallback(() => {
        setDragOverIndex(null);
    }, []);

    const handleDrop = useCallback((e: React.DragEvent<HTMLDivElement>, toIndex: number) => {
        e.preventDefault();
        if (disabled || draggedIndex === null || draggedIndex === toIndex) return;

        onReorder(draggedIndex, toIndex);
        setDraggedIndex(null);
        setDragOverIndex(null);
    }, [disabled, draggedIndex, onReorder]);

    return (
        <div className="image-preview">
            <div className="preview-grid">
                {images.map((image, index) => (
                    <div
                        key={image.id}
                        className={`preview-item ${draggedIndex === index ? 'dragging' : ''} ${dragOverIndex === index ? 'drag-over' : ''}`}
                        draggable={!disabled}
                        onDragStart={(e) => handleDragStart(e, index)}
                        onDragEnd={handleDragEnd}
                        onDragOver={(e) => handleDragOver(e, index)}
                        onDragLeave={handleDragLeave}
                        onDrop={(e) => handleDrop(e, index)}
                    >
                        <div className="preview-thumbnail">
                            <img src={image.thumbnail} alt={image.name} />
                            <span className="preview-index">{index + 1}</span>
                            {!disabled && (
                                <button
                                    className="preview-remove"
                                    onClick={() => handleRemove(index)}
                                    aria-label={`Remove ${image.name}`}
                                >
                                    <svg viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                                        <path d="M18 6L6 18M6 6l12 12" />
                                    </svg>
                                </button>
                            )}
                            {!disabled && (
                                <div className="preview-drag-handle" title="Drag to reorder">
                                    <svg viewBox="0 0 24 24" fill="currentColor">
                                        <path d="M8 6h2v2H8V6zm6 0h2v2h-2V6zM8 11h2v2H8v-2zm6 0h2v2h-2v-2zm-6 5h2v2H8v-2zm6 0h2v2h-2v-2z" />
                                    </svg>
                                </div>
                            )}
                        </div>
                        <div className="preview-info">
                            <span className="preview-name" title={image.name}>
                                {image.name}
                            </span>
                            <span className="preview-meta">
                                {image.imageData.width}  {image.imageData.height}  {formatSize(image.size)}
                            </span>
                        </div>
                    </div>
                ))}
            </div>
            {images.length > 1 && !disabled && (
                <p className="preview-hint">Drag images to reorder. First image is the alignment reference.</p>
            )}
        </div>
    );
}
