import type { MergeOptions } from '../lib/exposureFusion';

interface MergeSettingsProps {
    options: MergeOptions;
    onChange: (options: MergeOptions) => void;
    disabled?: boolean;
}

export function MergeSettings({ options, onChange, disabled }: MergeSettingsProps) {
    const handleSliderChange = (key: keyof MergeOptions, value: number) => {
        onChange({ ...options, [key]: value });
    };

    const handleToggle = (key: keyof MergeOptions, value: boolean) => {
        onChange({ ...options, [key]: value });
    };

    return (
        <div className={`merge-settings ${disabled ? 'disabled' : ''}`}>
            <div className="settings-group">
                <h3>Quality Weights</h3>
                <p className="settings-description">
                    Adjust how different pixel qualities influence the final blend
                </p>

                <div className="setting-item">
                    <label htmlFor="contrast-weight">
                        <span className="setting-label">Contrast</span>
                        <span className="setting-value">{options.contrastWeight.toFixed(2)}</span>
                    </label>
                    <input
                        id="contrast-weight"
                        type="range"
                        min="0"
                        max="2"
                        step="0.1"
                        value={options.contrastWeight}
                        onChange={(e) => handleSliderChange('contrastWeight', parseFloat(e.target.value))}
                        disabled={disabled}
                    />
                    <span className="setting-hint">
                        Higher = prefer pixels with more local detail
                    </span>
                </div>

                <div className="setting-item">
                    <label htmlFor="saturation-weight">
                        <span className="setting-label">Saturation</span>
                        <span className="setting-value">{options.saturationWeight.toFixed(2)}</span>
                    </label>
                    <input
                        id="saturation-weight"
                        type="range"
                        min="0"
                        max="2"
                        step="0.1"
                        value={options.saturationWeight}
                        onChange={(e) => handleSliderChange('saturationWeight', parseFloat(e.target.value))}
                        disabled={disabled}
                    />
                    <span className="setting-hint">
                        Higher = prefer more colorful pixels
                    </span>
                </div>

                <div className="setting-item">
                    <label htmlFor="exposure-weight">
                        <span className="setting-label">Exposure</span>
                        <span className="setting-value">{options.exposureWeight.toFixed(2)}</span>
                    </label>
                    <input
                        id="exposure-weight"
                        type="range"
                        min="0"
                        max="2"
                        step="0.1"
                        value={options.exposureWeight}
                        onChange={(e) => handleSliderChange('exposureWeight', parseFloat(e.target.value))}
                        disabled={disabled}
                    />
                    <span className="setting-hint">
                        Higher = prefer middle-brightness pixels, avoiding over/underexposure
                    </span>
                </div>
            </div>

            <div className="settings-group">
                <h3>Advanced</h3>

                <div className="setting-item setting-checkbox">
                    <label htmlFor="align-toggle">
                        <input
                            id="align-toggle"
                            type="checkbox"
                            checked={options.align}
                            onChange={(e) => handleToggle('align', e.target.checked)}
                            disabled={disabled}
                        />
                        <span className="setting-label">Auto Align Images</span>
                    </label>
                    <span className="setting-hint">
                        Enable if images were shot handheld (adds processing time)
                    </span>
                </div>

                <div className="setting-item">
                    <label htmlFor="pyramid-levels">
                        <span className="setting-label">Pyramid Levels</span>
                        <span className="setting-value">
                            {options.pyramidLevels === 0 ? 'Auto' : options.pyramidLevels}
                        </span>
                    </label>
                    <input
                        id="pyramid-levels"
                        type="range"
                        min="0"
                        max="10"
                        step="1"
                        value={options.pyramidLevels}
                        onChange={(e) => handleSliderChange('pyramidLevels', parseInt(e.target.value))}
                        disabled={disabled}
                    />
                    <span className="setting-hint">
                        0 = automatic. Higher = better blending for large images
                    </span>
                </div>
            </div>
        </div>
    );
}
